h1 {
  font-family: CircularStdBold;
  font-size: 58px;
  margin: 0 0 20px 0;
  color: #1d293f;
  letter-spacing: -1px;
}
h2 {
  font-family: CircularStdBold;
  font-size: 48px;
  margin: 0 0 20px 0;
  color: #1d293f;
  letter-spacing: -1px;
}
.btn {
  display: inline-block;
  background: #0f53fa;
  color: #fff;
  font-size: 16px;
  font-family: CircularStdMedium;
  padding: 18px 25px;
  text-decoration: none;
  border-radius: 5px;
}
.btn-alt {
  display: inline-block;
  background: #fff;
  border: 2px solid #e2e4e8;
  color: #1d293f;
  font-size: 16px;
  font-family: CircularStdMedium;
  padding: 16px 24px;
  text-decoration: none;
  border-radius: 5px;
}
.btn__icon {
  display: inline-block;
  vertical-align: middle;
  margin-right: 15px;
}
.page-pre {
  max-width: 510px;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}
.page-footer {
  background: #1d293f;
  position: relative;
  padding: 135px 0 0;
}
.page-footer__logo {
  display: inline-block;
  width: 60px;
  height: 60px;
  background: url("../img/svg/f-logo.svg") center/cover no-repeat;
  margin-bottom: 11px;
}
.page-footer__txt {
  font-size: 16px;
  line-height: 1.75;
  color: #8c97ac;
  margin-bottom: 30px;
}
.page-footer__btn {
  display: inline-block;
  color: #fff;
  background: #0f53fa;
  border-radius: 5px;
  padding: 15px 22px;
  font-size: 13px;
  text-decoration: none;
  font-family: CircularStdMedium;
}
.page-footer:before {
  content: "";
  display: inline-block;
  width: 255px;
  height: 255px;
  background: #ffd166;
  border-radius: 50%;
  position: absolute;
  left: -165.5px;
  bottom: -127.5px;
  animation: scale 3s infinite ease-in;
}
.page-footer:after {
  content: "";
  display: inline-block;
  width: 400px;
  height: 400px;
  background: #37def5;
  position: absolute;
  right: -324px;
  top: -239px;
  border-radius: 45px;
  transform: rotate(58deg);
  animation: rotate 7s infinite ease-in;
}
.page-footer-info__wrap {
  display: flex;
  justify-content: space-between;
  padding: 0 0 135px 0;
}
.page-footer-info__pre {
  width: 260px;
  margin-right: 50px;
}
.bottom-menu {
  display: flex;
  margin: -7px -75px 0 0;
}
.bottom-menu__item {
  padding-right: 75px;
}
.bottom-menu__top {
  color: #8c97ac;
  margin-bottom: 32px;
}
.bottom-menu__link {
  display: block;
  font-size: 16px;
  color: #e9f7fe;
  margin-bottom: 24px;
  text-decoration: none;
}
.bottom-menu__link:last-child {
  margin-bottom: 0;
}
.bottom-menu__msg {
  font-size: 16px;
  line-height: 1.75;
  color: #e9f7fe;
  margin: -3px 0 25px;
  max-width: 255px;
}
.bottom-menu__contact {
  display: block;
  font-size: 16px;
  color: #ffd166;
  margin-bottom: 10px;
  text-decoration: none;
}
.bottom-menu__contact:last-child {
  margin-bottom: 0;
}
.bottom {
  color: #8c97ac;
  font-size: 13px;
  position: relative;
  z-index: 5;
}
.bottom__wrap {
  border-top: 1px solid rgba(255,255,255,0.2);
  padding: 24px 0 28px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.socials {
  display: flex;
  align-items: center;
}
.socials__link {
  width: 18px;
  height: 22px;
  margin-right: 19px;
}
.socials__link:last-child {
  margin-right: 0;
}
.socials__link.m-twitter {
  background: url("../img/svg/twitter.svg") center/cover no-repeat;
  transform: translate(0, 1px);
}
.socials__link.m-fb {
  background: url("../img/svg/fb.svg") center/cover no-repeat;
}
.socials__link.m-google {
  background: url("../img/svg/google.svg") center/cover no-repeat;
}
